<template>
    <div class="loginWrap">
        <header>
            <router-link to="">
                <i class="el-icon-arrow-left" @click="goBack"></i>
            </router-link>
            <div>密码登录</div>
        </header>
        <div class="form">
            <section class="input_c">
                <input type="text" placeholder="账号" v-model="username">
            </section>
            <section class="input_c">
                <input :type="value ? 'text' : 'password'" placeholder="密码" v-model="password">
                <el-switch v-model="value" active-color="#13ce66" inactive-color="#cccccc">
                </el-switch>
            </section>
            <section class="input_c">
                <input type="text" placeholder="验证码" v-model="capCode">
                <div class="verification" @click="requestCode">
                    <img :src="codeUrl" alt="">
                    <div>
                        <p>看不清</p>
                        <p>换一张</p>
                    </div>
                </div>
            </section>
        </div>
        <p class="tips">温馨提示：未注册过的账号，登录时将自动注册</p>
        <p class="tips">注册过的用户可凭账号密码登录</p>
        <div class="loginBtn" @click="loginAction">登录</div>
        <router-link to="">重置密码？</router-link>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data(){
            return {
                value:false,
                username:"",
                password:"",
                capCode:"",
                codeUrl:""
            }
        },
        created(){
           this.requestCode();
        },
        methods:{
            goBack(){
                this.$router.go(-1);
            },
            loginAction(){
                this.axios.post("/v2/login",{
                    username:this.username,
                    password:this.password,
                    captcha_code:this.capCode
                }).then(res=>{
                    console.log(res);
                    if(res.user_id){
                        this.$router.push("/home");
                        localStorage.setItem("loginname",res.username);
                    }else {
                        console.log(res.message);
                    }
                }).catch(err=>{});
            },
            requestCode(){
                this.axios.post("/v1/captchas").then(res=>{
                    this.codeUrl = res.code;
                }).catch(err=>{});
            }
        }
    }
</script>

<style scoped>
    .loginWrap {
        padding-top: 1.95rem;
    }
    header>a {
        color: #fff;
        line-height: 2rem;
        margin-left: .4rem;
    }
    header>div {
        width: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight: 700;
    }
    .form {
        background-color: #fff;
        margin-top: .6rem;
    }
    .input_c {
        display: flex;
        justify-content: space-between;
        padding: .6rem .8rem;
        border-bottom: 1px solid #f1f1f1;
    }
    .input_c input {
        font-size: .7rem;
        color: #666;
        border: none;
        outline: none;
    }
    .verification {
        display: flex;
        align-items: center;
    }
    .verification>img {
        width: 3.5rem;
        height: 1.5rem;
        margin-right: .2rem;
    }
    .verification p{
        font-size: .55rem;
        color: #666;
    }
    .verification p+p {
        color: #3b95e9;
        margin-top: .2rem;
    }
    .tips {
        font-size: .5rem;
        color: red;
        padding: .4rem .6rem;
        line-height: .5rem;
    }
    .loginBtn {
        margin: 0 .5rem 1rem;
        font-size: .7rem;
        color: #fff;
        background-color: #4cd964;
        padding: .5rem 0;
        border: 1px;
        border-radius: .15rem;
        text-align: center;
    }
    .loginWrap>*:last-child {
        float: right;
        font-size: .6rem;
        color: #3b95e9;
        margin-right: .3rem;
    }
</style>